import React, { useState, useEffect } from "react";
import ModalMap from "./ModalMap";
import { Modal, Button } from "react-bootstrap";

const EarthquakeModal = ({ closeModal, earthquakeData }) => {
  const [showModal, setShowModal] = useState(false);
  const [mapModalVisible, setMapModalVisible] = useState(false);

  useEffect(() => {
    if (earthquakeData) {
      setShowModal(true);
    } else {
      setShowModal(false);
    }
  }, [earthquakeData]);

  const openMapModal = () => {
    setMapModalVisible(true);
    closeModal();
  };

  const closeMapModal = () => {
    setMapModalVisible(false);
  };

  const handleCloseModal = () => {
    setShowModal(false);
    closeModal();
  };

  return (
    <>
      {showModal && (
        <Modal show={showModal} onHide={handleCloseModal}>
          <Modal.Header closeButton>
            <Modal.Title> .</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p>: {earthquakeData.loc}</p>
            <p>: {earthquakeData.lat}</p>
            <p>: {earthquakeData.lng}</p>
            <p>: {earthquakeData.mt}</p>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleCloseModal}>
              .
            </Button>
            <Button variant="primary" onClick={openMapModal}>
                
            </Button>
          </Modal.Footer>
        </Modal>
      )}
      {mapModalVisible && (
        <Modal show={mapModalVisible} onHide={closeMapModal}>
          <Modal.Body>
            <ModalMap
              lat={Number(earthquakeData.lat)}
              lng={Number(earthquakeData.lng)}
              mapVisible={mapModalVisible}
            />
          </Modal.Body>
        </Modal>
      )}
    </>
  );
};

export default EarthquakeModal;
