import React, { useState, useEffect } from "react";
import styles from "../styles/footer.module.css";
import { Modal, Button } from "react-bootstrap";
import EarthquakeModal from "./modal/EarthquakeModal";
import { fetchEarthquakeData } from "../utils/api";
import { useMediaQuery } from "react-responsive";

const Footer = ({ isSidebarOpen }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [earthquakeData, setEarthquakeData] = useState({
    loc: "",
    lat: "",
    lng: "",
    mt: "",
  });
  const [modalData, setModalData] = useState({
    loc: "",
    lat: "",
    lng: "",
    mt: "",
  });

  const isPC = useMediaQuery({
    query: "(min-width:820px)",
  });

  const isMobile = useMediaQuery({
    query: "(max-width:819px)",
  });

  const handleModalInputChange = (e, field) => {
    setModalData({ ...modalData, [field]: e.target.value });
  };

  useEffect(() => {
    //      
    fetchData();
    // 20   
    const interval = setInterval(fetchData, 20000);

    return () => {
      clearInterval(interval);
    };
  }, []);

  const fetchData = async () => {
    const data = await fetchEarthquakeData();
    setEarthquakeData(data);
  };

  const handleModalClose = () => {
    setIsModalOpen(false);
    setModalData({
      loc: "",
      lat: "",
      lng: "",
      mt: "",
    });
  };

  const handleModalSave = () => {
    setEarthquakeData(modalData);
    setIsModalOpen(false);
  };

  return (
    <>
      {isModalOpen && (
        <Modal show={isModalOpen} onHide={handleModalClose}>
          <Modal.Header closeButton>
            <Modal.Title>  </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <label>
              
              <input
                type="text"
                className="form-control"
                value={modalData.loc}
                onChange={(e) => handleModalInputChange(e, "loc")}
                placeholder=" ."
              />
            </label>
            <label>
              
              <input
                type="text"
                className="form-control"
                value={modalData.lat}
                onChange={(e) => handleModalInputChange(e, "lat")}
                placeholder=" ."
              />
            </label>
            <label>
              
              <input
                type="text"
                className="form-control"
                value={modalData.lng}
                onChange={(e) => handleModalInputChange(e, "lng")}
                placeholder=" ."
              />
            </label>
            <label>
              
              <input
                type="text"
                className="form-control"
                value={modalData.mt}
                onChange={(e) => handleModalInputChange(e, "mt")}
                placeholder=" ."
              />
            </label>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleModalClose}>
              
            </Button>
            <Button variant="primary" onClick={handleModalSave}>
              
            </Button>
          </Modal.Footer>
        </Modal>
      )}
      {earthquakeData?.lat &&
        earthquakeData?.lng &&
        earthquakeData?.mt &&
        earthquakeData?.loc && (
          <EarthquakeModal
            closeModal={() => setIsModalOpen(false)}
            earthquakeData={earthquakeData}
          />
        )}
      <footer className={`${styles.footer} ${isSidebarOpen && styles.open}`}>
        <div className={styles.footer_container}>
          <div className={styles.footer_item}>
            <span className={styles.footer_item_title}></span>
            <div>&copy;2023</div>
          </div>
          {isPC && (
            <>
              <div className={styles.footer_item}></div>
              <div className={styles.footer_item}></div>
            </>
          )}
          <div className={styles.footer_item}>
            <Button
              size="lg"
              className="mb-2"
              variant="dark"
              onClick={() => setIsModalOpen(true)}
            >
              Test Modal
            </Button>
            <div>
              loc:  <br />
              lat: 37.55840227 <br />
              lng: 126.99779874
            </div>
          </div>
        </div>
      </footer>
    </>
  );
};

export default Footer;
