import React from "react";
import { useState } from "react";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import "bootstrap/dist/css/bootstrap.min.css";
import "../../styles/rule/rule.css";
import { Routes, Route, Link } from "react-router-dom"; //  
import SubPage1 from "./SubPage1";
import SubPage2 from "./SubPage2";

const Rule = () => {
  const [activeButton, setActiveButton] = useState("");

  const handleButtonClick = () => {
    setActiveButton("");
  };

  const handleSecondButtonClick = () => {
    setActiveButton("subpage1");
  };

  const handleThirdButtonClick = () => {
    setActiveButton("subpage2");
  };

  return (
    <>
      <Header />
      <main
        className="main"
        style={{
          width: "85vw",
          maxWidth: "800px",
          margin: "0 auto",
          marginBottom: "80px",
        }}
      >
        <div className="main_title">,  .</div>
        <div className="btn_select_group">
          <Link
            to="/rule"
            className={`select_button ${
              activeButton === "" ? "color_blue" : "color_white"
            }`}
            onClick={handleButtonClick}
          >
             
          </Link>
          <Link
            to="/rule/subpage1"
            className={`select_button ${
              activeButton === "subpage1" ? "color_blue" : "color_white"
            }`}
            onClick={handleSecondButtonClick}
          >
              
          </Link>
          <Link
            to="/rule/subpage2"
            className={`select_button ${
              activeButton === "subpage2" ? "color_blue" : "color_white"
            }`}
            onClick={handleThirdButtonClick}
          >
              
          </Link>
        </div>
        <Routes>
          <Route
            path="/"
            element={
              <div className="main_text">
                <ol>
                  <li className="ol">   .</li>
                  <ul className="ul list-unstyled">
                    <li className="ul">
                               
                         .
                    </li>
                    <li className="ul">
                              
                             .
                    </li>
                    <li className="ul">
                             
                      .
                    </li>
                    <li className="ul">
                             .
                    </li>
                  </ul>
                  <li className="ol">
                         .
                  </li>
                  <ul className="list-unstyled">
                    <li className="ul">
                             .
                    </li>
                    <li className="ul">
                      ,         
                       .
                    </li>
                    <li className="ul">
                             .
                    </li>
                    <li className="ul">
                              
                      .
                    </li>
                  </ul>
                  <li className="ol">  .</li>
                  <ul className="list-unstyled">
                    <li className="ul">    .</li>
                    <li className="ul">
                         ,   
                      .
                    </li>
                    <li className="ul">
                           
                      .
                    </li>
                  </ul>
                  <li className="ol">
                         .
                  </li>
                  <ul className="list-unstyled">
                    <li className="ul">
                            .
                    </li>
                    <li className="ul">
                                
                       .
                    </li>
                    <li className="ul">
                             .
                    </li>
                    <li className="ul">
                           .
                    </li>
                  </ul>
                  <li className="ol">    .</li>
                  <ul className="list-unstyled">
                    <li className="ul">
                          ,  
                        .
                    </li>
                    <li className="ul">
                               ,
                        .
                    </li>
                  </ul>
                </ol>
              </div>
            }
          />
          <Route path="/subpage1" element={<SubPage1 />} />
          <Route path="/subpage2" element={<SubPage2 />} />
        </Routes>
      </main>

      <Footer />
    </>
  );
};

export default Rule;
